"use client";

import Image from "next/image";
import Link from "next/link";

const industries = [
  {
    title: "SportsTech",
    description: "Innovative solutions designed for athletes, teams, and sports enthusiasts.",
    image: "/industries/sportstech.jpg",
    link: "/industries/sportstech",
  },
  {
    title: "Real Estate",
    description: "Streamlined user experience for buying, selling, and renting property.",
    image: "/industries/realestate.jpg",
    link: "/industries/realestate",
  },
  {
    title: "E-Commerce",
    description: "Designing scalable and delightful online shopping experiences.",
    image: "/industries/ecom.jpg",
    link: "/industries/ecom",
  },
  {
    title: "Fintech",
    description: "Building trust with modern UI for finance and investment platforms.",
    image: "/industries/fintech.jpg",
    link: "/industries/fintech",
  },
  {
    title: "Telecom",
    description: "Helping telecom brands simplify complex services for users.",
    image: "/industries/telecom.jpg",
    link: "/industries/telecom",
  },
  {
    title: "EdTech",
    description: "Crafting intuitive educational platforms for learners and teachers.",
    image: "/industries/edtech.jpg",
    link: "/industries/edtech",
  },
  {
    title: "Healthcare",
    description: "Human-centered designs for clinics, hospitals, and wellness apps.",
    image: "/industries/healthcare.jpg",
    link: "/industries/healthcare",
  },
  {
    title: "Agriculture",
    description: "Empowering farmers and supply chains through smart digital tools.",
    image: "/industries/agriculture.jpg",
    link: "/industries/agriculture",
  },
  {
    title: "Enterprise",
    description: "Design systems for complex enterprise software made simple.",
    image: "/industries/enterprise.jpg",
    link: "/industries/enterprise",
  },
];

export default function IndustriesPage() {
  return (
    <section className="px-6 py-16 bg-gray-50 text-gray-800">
      <div className="max-w-6xl mx-auto">
        <h1 className="text-4xl font-bold text-center mb-4">
          Reimagining industries through design
        </h1>
        <p className="text-center text-gray-600 mb-12 max-w-2xl mx-auto">
          From agriculture, e-commerce, edtech and enterprise solutions to fintech, healthcare, hospitality, IoT and HR solutions, we have designed it all.
        </p>

        <div className="grid grid-cols-1 gap-15">
          {industries.map((industry, idx) => (
            <div
              key={idx}
              className="flex flex-col md:flex-row bg-white rounded-xl shadow hover:shadow-md overflow-hidden"
            >
              <div className="w-full md:w-1/2 h-80 relative">
                <Image
                  src={industry.image}
                  alt={industry.title}
                  fill
                  className="object-cover"
                />
              </div>
              <div className="w-full md:w-1/2 p-6 flex flex-col justify-between">
                <div>
                  <h3 className="text-xl font-semibold mb-2">{industry.title}</h3>
                  <p className="text-gray-600 mb-4">{industry.description}</p>
                </div>
                <Link
                  href={industry.link}
                  className="text-sm font-medium text-blue-600 hover:underline"
                >
                  Read More â†’
                </Link>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}
